<template>
  <div class="home">
    <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOutRight">
      <el-card class="box-card" v-show="show">
        <div>
          <h1>尊敬的用户： {{$store.state.address}}</h1>
          <div>您好！</div>
          <div class="info">星云电子合同是基于星云链（NAS）的一个区块链应用。</div>
          <div class="info">在这里，您只需要拥有星云区块链钱包，就可以起草合同，与他人签订合同。</div>
          <div class="info">合同一旦签订以后（经过双方确认），在星云链中此合同就无法再被更改，从而实现了合同的防篡改，抗抵赖性。</div>
          <div class="info">使用星云电子合同系统，只要您拥有星云钱包，知道对方的星云钱包地址，就能与对方签订合同，足不出户，不需要见面，就能签订不可篡改不可抵赖的合同，极大的方便了人们的工作效率。</div>
        </div>
        <el-button type="primary" @click="$router.push('/my')">立即进入</el-button>
      </el-card>
    </transition>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: false
    }
  },
  mounted () {
    this.show = true
  }
}
</script>

<style lang="less">
  .home {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-image: url('../assets/images/bg.jpg');
    .box-card {
      width: 800px;
      background: rgba(0, 0, 0, 0.44);
      color: #fff;
      font-size: 20px;
      line-height: 34px;
      .el-card__body {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        height: 500px;
      }
      h1 {
        padding-bottom: 20px;
      }
      .info {
        text-indent: 40px;
        margin-top: 10px;
      }
    }
  }
</style>


